document.addEventListener('DOMContentLoaded', function() {
    darkMode();
    fondoAuxiliar();
});

function darkMode(){
    const prefiereDarkMode = window.matchMedia("(prefers-color-scheme: ligth)");
    if (prefiereDarkMode.matches) {
        document.body.classList.add("dark-mode");
    } else {
        document.body.classList.remove("dark-mode");
    }
    prefiereDarkMode.addEventListener("change", function() {
        if (prefiereDarkMode.matches) {
            document.body.classList.add("dark-mode");
        } else {
            document.body.classList.remove("dark-mode");
        }
    })
    const botonDarkMode = document.querySelector(".dark-mode-boton");
    botonDarkMode.addEventListener("click", function() {
        document.body.classList.toggle("dark-mode");
    });
}

function fondoAuxiliar(){
    if(document.querySelector(".dark-mode")){
        const html = document.querySelector(".html-auxiliar");
        console.log("Detecta el .dark-mode");
        console.log(html);
    } else {
        console.log("NO hay dark-mode");
    }
}